import React, { forwardRef, useImperativeHandle, useEffect } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { bindActionCreators } from "redux";
import * as Actions from "../../store/User/UserAction";
import { store } from "react-notifications-component";

const ScreenRecording = forwardRef((props, ref) => {
  const [capturing, setCapturing] = React.useState(false);
  const mediaRecorderRef = React.useRef(null);
  const [abort, setAbort] = React.useState(false);
  const [access, setAccess] = React.useState(false);
  const [stream, setStream] = React.useState(false);
  const [recordedChunks, setRecordedChunks] = React.useState([]);
  let isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);

  const { capturingNew, abortNew, accessNew, screenRecordedFile, streamNew } =
    props.UserReducer;

  useEffect(() => {
    //  if(capturing !== capturingNew){

    //  }
    console.log(accessNew);
    setCapturing(capturingNew);
    setAccess(accessNew);
    //  console.log(abortNew)
    //  if(abort != abortNew){
    setAbort(abortNew);
    //  }
    if (streamNew) {
      setStream(streamNew);
    }
    // if(screenRecordedFile.length){
    //   setRecordedChunks(screenRecordedFile)
    // }
    //  console.log('insidee',capturingNew,abortNew,accessNew,screenRecordedFile,streamNew)
  }, [capturingNew, abortNew, accessNew, screenRecordedFile]);

  useImperativeHandle(ref, () => ({
    getAccess() {
      getAccess();
    },
    startCapture() {
      if (access) {
        handleStartCaptureClick();
      }
    },
    stopCapture() {
      if (capturing) {
        handleStopCaptureClick();
      }
    },
    abortCapture() {
      if (capturing) {
        handleAbortCaptureClick();
      }
    },
    closeAccess() {
      if (access) {
        closeAccess();
      }
    },
  }));

  const getAccess = React.useCallback(async () => {
    let streamCopy = await navigator.mediaDevices.getDisplayMedia({
      video: {
        mediaSource: "screen",
        frameRate: 5,
        height: 720,
        videoBitsPerSecond: 625000,
      },
    });
    const videoTrack = await streamCopy.getVideoTracks()[0];
    const screenDetail = await videoTrack.getSettings();

    if (
      streamCopy &&
      screenDetail &&
      screenDetail.displaySurface.toLowerCase() === "monitor"
    ) {
      setStream(streamCopy);
      setAccess(true);
      props.UserAction.addStream(streamCopy);
      props.UserAction.getAccess();
      props.onScreenAccess();
    } else {
      setAccess(false);
      props.UserAction.closeAccess();
      
          store.addNotification({
            title: "Error",
            message: "You need to provide Entire Screen Access to Continue",
            type: "danger",
            container: "top-center",
            animationIn: ["animated", "fadeIn"],
            animationOut: ["animated", "fadeOut"],
            dismiss: {
              duration: 2000,
            },
          });

          setTimeout(()=>{
          getAccess()
          },3000)
    }
    streamCopy.getVideoTracks()[0].onended = function () {
      handleUserStop();
    };
  }, [access, stream, setAccess, setStream]);

  const handleUserStop = React.useCallback(() => {
    setAbort(true);
    setCapturing(false);
    setAccess(false);

    props.UserAction.abortCapture(true);
    props.UserAction.stopCapture();
    props.UserAction.closeAccess();

    props.onUserStop();
    setRecordedChunks([]);
  }, [access, setAccess, mediaRecorderRef, setCapturing, setAbort]);

  const handleStartCaptureClick = React.useCallback(() => {
    mediaRecorderRef.current = new MediaRecorder(stream, {
      mimeType: isSafari
        ? 'video/mp4; codecs="avc1.42E01E, mp4a.40.2"'
        : "video/webm",
    });
    mediaRecorderRef.current.start();
    console.log(mediaRecorderRef.current);
    props.onRecordStart();
    setCapturing(true);
    setAbort(false);

    props.UserAction.startCapture();
    props.UserAction.abortCapture(false);
    // console.log('xyzzzzz')
    mediaRecorderRef.current.addEventListener(
      "dataavailable",
      handleDataAvailable
    );
  }, [access, stream, setCapturing, setAbort, mediaRecorderRef]);

  const handleDataAvailable = React.useCallback(
    ({ data }) => {
      console.log("data", data);
      if (data.size > 0) {
        console.log("data availbale");
        setRecordedChunks((prev) => prev.concat(data));
      }
    },
    [setRecordedChunks]
  );

  const handleStopCaptureClick = React.useCallback(() => {
    mediaRecorderRef.current.stop();
    props.onRecordStop();
    setCapturing(false);
    props.UserAction.stopCapture();
  }, [mediaRecorderRef, setCapturing]);

  const handleAbortCaptureClick = React.useCallback(() => {
    setAbort(true);
    setCapturing(false);
    props.UserAction.abortCapture(true);
    props.UserAction.stopCapture();
    mediaRecorderRef.current.stop();
    props.onRecordStop();
    setRecordedChunks([]);
    props.onAbort();
  }, [mediaRecorderRef, setCapturing, setAbort]);

  const closeAccess = React.useCallback(() => {
    stream.getTracks().forEach(function (track) {
      console.log(track);
      if (track.readyState == "live") {
        track.stop();
      }
    });
    console.log("inside2");
    // setAccess(false);
    // props.UserAction.closeAccess()
  }, [stream, access]);

  const reset = () => {
    console.log("inside3");
    // setAccess(false);
    setAbort(false);
    setCapturing(false);
    setRecordedChunks([]);

    props.UserAction.stopCapture();
    props.UserAction.abortCapture(false);
    // props.UserAction.closeAccess()
  };
  console.log("recorded", recordedChunks.length, capturing, abort, access);
  if (recordedChunks.length > 0 && capturing == false && abort == false) {
    // console.log('insideee recorded')
    const blob = new Blob(recordedChunks, {
      type: "video/webm",
    });
    // props.UserAction.addScreenRecording(recordedChunks)
    props.onVideoFile(blob);
    reset();
  } else if (recordedChunks.length > 0 && capturing == false && abort == true) {
    reset();
  }

  return (
    <div>
      {/* <button onClick={getAccess}>Get Access</button>
      {
        access?<button onClick={handleStartCaptureClick}>Start Recording</button>:",no access"
      }
      {
        capturing?<button onClick={handleStopCaptureClick}>Stop Recording</button>:",not recording yet"
      } */}
      {/* {
        console.log(capturing,access)
      } */}
    </div>
  );
});

const mapStateToProps = (state) => ({
  UserReducer: state.UserReducer,
});

function mapDispatchToProps(dispatch) {
  return {
    UserAction: bindActionCreators(Actions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps, null, {
  forwardRef: true,
})(ScreenRecording);
